.contents {
  display: -ms-flexbox;
  display: flex;
}

.main {
  height: 100%;
  width: 100%;
}

.header {
  position: fixed;
  width: 100%;
  height: 40px;
}

.sidebar {
  margin: 0;
  height: 800px;
  width: 220px;
  background-color: black;
}

.sidebar li {
  padding-left: 0;
  padding-top: 30px;
}

.sidebar a {
  text-decoration: none;
  padding: 10px;
  color: hsla(0, 0%, 100%, 0.6);
  list-style: none;
}

.sidebar a:hover {
  color: white;
}



